import DefMask from '@/components/content/defmask/DefMask.vue'

export default {
	components: {
		DefMask
	},
	data() {
		return {
			/* 常规模块 */
			
			//输入栏文字
			msgContent: '',
			
			//无工具栏的底部高度
			btmHeight1: 0,
			
			//有工具栏的底部高度
			btmHeight2: 0,
			
			
			//session-content无工具栏时的定位样式
			contentStyle1: '',
			
			//session-content有工具栏时的定位样式
			contentStyle2: '',
			
			//显示键盘
			showKeyboard: true,
			
			//是否聚焦输入框
			isFocus: false,
			
			//显示工具栏
			showTool: false,
			
			//键盘弹起时是否上推页面
			adjust: true,
			
			
			
			/** 语音输入模块 **/
			
			//语音计时器
			speakTimer: null,
			
			//语音计时
			speakCount: 0,
			
			//说话模式
			isSpeak: false,
			
			speakingTitle: '按住 说话',
			
			//说话时上滑到一定位置显示取消按钮
			showCancel: false,
			
			//触摸到取消按钮时
			cancelActive: false,
			
			//触摸到转文字按钮时
			translateActive: false,
			
			//存储取消与转文字按钮节点坐标信息
			cancelInfo: {
				'left1': 0,
				'right1': 0,
				'left2': 0,
				'right2': 0,
				'top': 0,
				'bottom': 0
			}
		}
	},
	
	
	computed: {
		calcSpeakCount() {
			if(this.speakCount<10) {
				return `0${this.speakCount}`
			}else {
				return this.speakCount
			}
		}
	},
	watch: {
		//监听工具栏的显示与隐藏，从而重新获取底部高度让content重新定位
		// showTool(newV, oldV) {
		// 	console.log(newV, oldV)
		// 	if(newV !== oldV) {
		// 		this.$nextTick(() => {
		// 			this.getBtmHeight()
		// 		})
		// 	}
		// }
		
	},
	methods: {
		
		//监听按住说话手指移动
		moveHandle(e) {
			if(e.touches[0].clientY < 540) {
				
				console.log(e)
				this.showCancel = true
				
				
			}else {
				this.showCancel = false
			}
			if(this.showCancel) {
				if(this.cancelInfo.right2 === 0) {
					this.$nextTick(() => {
						const query = uni.createSelectorQuery().in(this)
						query.select('.cancel-item').boundingClientRect(data => {
							
							this.cancelInfo.left1 = data.left
							this.cancelInfo.right1 = data.right
							this.cancelInfo.top = data.top
							this.cancelInfo.bottom = data.bottom
							console.log(this.cancelInfo)
						}).exec()
						
						query.select('.translate-item').boundingClientRect(data => {
							
							this.cancelInfo.left2 = data.left
							this.cancelInfo.right2 = data.right
							console.log(this.cancelInfo)
							return
						}).exec()
					})
				}else {
					if(e.touches[0].clientY>this.cancelInfo.top && e.touches[0].clientY<this.cancelInfo.bottom) {
						if(e.touches[0].clientX>this.cancelInfo.left1 && e.touches[0].clientX<this.cancelInfo.right1) {
							this.$nextTick(() => {
								this.cancelActive = true
							})
						}else if(e.touches[0].clientX>this.cancelInfo.left2 && e.touches[0].clientX<this.cancelInfo.right2) {
							this.$nextTick(() => {
								this.translateActive = true
							})
						}else {
							this.$nextTick(() => {
								this.cancelActive = false
								this.translateActive = false
							})
						}
					}else {
						this.$nextTick(() => {
							this.cancelActive = false
							this.translateActive = false
						})
					}
					
				}
			}
			
			
			
			
		},
		//按住说话
		showSpeaking() {
			this.isSpeak = true
			this.speakingTitle = '松开 结束'
			this.$refs.speakMask.isShowMask = true
			this.speakTimer = setInterval(() => {
				this.speakCount += 1
			}, 1000)
			this.speakTimer
		},
		
		//松开结束说话
		hideSpeaking() {
			if(this.cancelActive) {
				console.log('取消语音')
			}else if(this.translateActive) {
				console.log('将语音转成文字')
			}else {
				console.log('发送语音')
			}
			
			
			this.isSpeak = false
			this.speakingTitle = '按住 说话'
			this.showCancel = false
			this.cancelActive = false
			this.translateActive = false
			clearInterval(this.speakTimer)
			this.speakCount = 0
			this.$refs.speakMask.isShowMask = false
		},
		
		//取消
		handleCancel(e) {
			console.log('取消')
			console.log(e)
		},
		
		//转文字
		handleTranslate(e) {
			console.log('转文字')
			console.log(e)
		},
		
		//改变为说话模式
		doSpeak() {
			this.$nextTick(() => {
				this.showKeyboard = false
				this.isFocus = false
			})
		},
		
		//改变为输入模式
		doInput() {
			this.$nextTick(() => {
				this.showKeyboard = true
				this.isFocus = true
			})
		},
		
		//监听输入
		handleInput(e) {
			console.log(e)
		},
		
		//解决 当工具栏出现时，点击输入框聚焦页面不上推问题
		updateFocus() {
			if(this.showTool) {
				this.showTool = false
			}
			setTimeout(() => {
				this.$nextTick(() => {
					this.isFocus = false
					this.adjust = false
					setTimeout(() => {
						this.isFocus = true
						this.adjust = true
					}, 80)
				})
			}, 80)
		},
		
		//聚焦时触发
		handleFocus() {
			
		},
		
		//触摸页面隐藏键盘和工具栏和表情等
		hideAll() {
			if(this.showTool) {
				this.$nextTick(function(){
					setTimeout(() => {
						this.showTool = false
					}, 200)
				})
			}
			if(this.isFocus) {
				this.$nextTick(function(){
					setTimeout(() => {
						this.isFocus = false
					}, 200)
				})
			}
		},
		//工具栏的显示
		handleTool() {
			
			this.showTool = !this.showTool
			
			setTimeout(() => {
				//只获取一次工具栏高度
				if(this.btmHeight2 === 0) {
					const query = uni.createSelectorQuery().in(this)
					query.select('.session-btm').boundingClientRect(data => {
						const h = data.height
						



						

						this.contentStyle2 = `top: 0; right: 0; bottom: ${h}rpx; left: 0; `

						this.btmHeight2 = h
					}).exec()
				}
			}, 80)
			
		},
		
		//获取底部无工具栏时的高度,改变content定位
		getBtmHeight() {
			const query = uni.createSelectorQuery().in(this)
			query.select('.session-btm').boundingClientRect(data => {
				const h = data.height



				

				this.contentStyle1 = `top: 0; right: 0; bottom: ${h}rpx; left: 0; `

				
				console.log(h)
			}).exec()
		},
		
		//跳转到GroupInfo
		toGroupInfo() {
			uni.navigateTo({
				url: '/components/content/session/sessionSon/GroupInfo'
			})
		},
		
		//跳转到ChatFriendSetting
		toChatFriendSetting() {
			uni.navigateTo({
				url: '/components/content/session/sessionSon/ChatFriendSetting'
			})
		}
	},
	onLoad(option) {
		if(option.name) {
			uni.setNavigationBarTitle({
				title: `${option.name}(离线)`
			})
		}
		
	},
	mounted() {
		
		this.getBtmHeight()
	},
	
	onBackPress() {
		if(this.showTool) {
			this.showTool = false
			return true
			// if(e.from === 'navigateBack') {
			// 	//返回
			// 	return false
			// }
			// if(e.from === 'backbutton') {
			// 	uni.showModal({
			// 		title: '提示',
			// 		content: '您确定退出当前编辑？',
			// 		success(res) {
			// 			if(res.confirm) {
			// 				uni.navigateBack()
			// 			}else if(res.cancel){
			// 				//表示禁止默认返回
			// 				return true
			// 			}
			// 		},
			// 		fail(err) {
			// 			console.log(err)
			// 			return true
			// 		}
			// 	})
			// }
		}else {
			return false
		}
	},
	onPageScroll(e) {
		console.log(e)
		
		return
		if(this.isFocus) {
			this.$nextTick(() => {
				this.isFocus = false
			})
		}
		//页面滚动时隐藏键盘和工具栏
		if(this.showTool) {
			this.$nextTick(() => {
				this.showTool = false
			})
		}
	},
	onNavigationBarButtonTap(option) {
		if(option.index === 0) {
			this.toGroupInfo()
		}
	},
	onHide() {
		clearInterval(this.speakTimer)
	}
}
